@import './../global';

@keyframes top {
    0% {
        height: 70%;
    }

    100% {
        height: 40%;
    }
}

@keyframes bot992 {
    0% {
        height: 70%;
    }

    100% {
        height: 40%;
    }
}

@keyframes bot1200 {
    0% {
        height: 70%;
    }

    100% {
        height: 40%;
        bottom: -200px;
        // @media (max-width: 1200px) {
        //     bottom: 100px;
        // }

    }
}

@keyframes logo {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1
    }
}

header {
    position: relative;
    z-index: 1;
    height: 100vh;
    min-height: 950px;
    padding: 30px 0;

    &::before {
        content: '';
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;

        height: 70%;
        animation-duration: 1.5s;
        animation-name: top;
        animation-fill-mode: both;

        background-image: url(./../images/header_top.png);
        background-position: bottom;
        background-size: cover;
        background-repeat: no-repeat;


        @media (max-width: 1200px) {
            height: 200px;
        }
    }

    &::after {
        content: '';
        position: absolute;
        z-index: 2;
        bottom: 0;
        height: 70%;
        // bottom: -350px;
        left: 0;
        right: 0;

        animation-duration: 1.5s;
        animation-fill-mode: both;
        animation-timing-function: ease;
        animation-name: bot1200;

        background-image: url(./../images/header_bot.png);
        background-position: top;
        background-size: cover;
        background-repeat: no-repeat;

        @media (max-width:1200px) {
            animation-name: bot992;
        }

    }

    #myVideo {
        position: absolute;
        z-index: 1;
        right: 0;
        bottom: -10%;
        // position: bottom;
        min-width: 100%;
        min-height: 120%;
    }


    .container {
        position: relative;
        z-index: 10;
        margin: 0 auto;
        width: 1200px;

        @media (max-width: 1200px) {
            width: 992px;
        }


        ul {
            margin: 0 auto;
            margin-bottom: 150px;
            display: grid;
            width: 200px;
            justify-content: center;
            align-items: center;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            gap: 30px;

            @media (max-width: 1200px) {
                margin-bottom: 100px;
            }

            li {
                display: grid;
                align-items: center;
                height: 24px;
                width: 24px;

                img {
                    width: 100%;
                    height: 100%;
                }
            }
        }

        .logo {
            display: grid;
            justify-content: center;
            align-items: center;
            margin-bottom: 150px;

            animation-duration: 1s;
            animation-delay: 1s;
            animation-fill-mode: both;
            animation-timing-function: ease;
            animation-name: logo;

            opacity: 0;


            .textLogo {
                text-transform: uppercase;
                font-size: 390px;
                font-family: Averia, sans-serif;
                font-weight: bold;
                text-shadow: 0 45px 49px $mainColor;

                color: #fff;

                @media (max-width: 1200px) {
                    font-size: 270px;
                }
            }
        }

        .text {
            margin-bottom: 50px;

            text-align: center;
            font-size: 14px;

            color: $mainColor;
        }

        .play {
            margin: 0 auto;
            margin-bottom: 50px;
            width: 70px;
            height: 70px;
            display: grid;
            justify-content: center;
            align-items: center;
            border-radius: 50px;
            border-bottom: 2px solid $mainColor;

            box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

            .hidden {
                display: none;
            }
        }

        #scrollDown {
            margin: 0 auto;
            display: grid;
            grid-template-columns: min-content 1fr;
            width: max-content;
            justify-content: center;
            gap: 10px;

            .textScroll {
                font-size: 16px;

                color: $mainColor
            }
        }
    }
}

#content {
    position: relative;
    z-index: 10;
    height: 100vh;
    margin-top: 100px;

    background-color: $mainColor;
}